@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap');

body {
    background-color: #334155;
    position: relative;
    z-index: -1;
}

.content-app {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.footer {
    margin-top: auto;
}

.song-text {
    font-size: 12px;
    font-weight: 500;
}

@media (min-width: 280px) {
    .song-text {
        font-size: 12px;
        font-weight: 500;
    }
    .title-song {
        font-size: 18px; 
    }
}

@media (min-width: 360px) {
    .song-text {
        font-size: 16px;
        font-weight: 500;
    }
    .title-song {
        font-size: 22px; 
    }
}

@media (min-width: 375px) {
    .song-text {
        font-size: 16px;
        font-weight: 500;
    }
    .title-song {
        font-size: 22px; 
    }
}

@media (min-width: 400px) {
    .song-text {
        font-size: 18px;
        font-weight: 500;
    }
    .title-song {
        font-size: 24px; 
    }
}

@media (min-width: 600px) {
    .song-text {
        font-size: 34px;
        font-weight: 500;
    }
    .title-song {
        font-size: 42px; 
    }
}

@media (min-width: 900px) {
    .song-text {
        font-size: 40px;
        font-weight: 500;
    }
    .title-song {
        font-size: 45px; 
    }
}